//      

const Color = require('../style-spec/util/color');
const util = require('../util/util');

                                     
             
                  
                  
                   
                    
                  
                  
                    
                 
                 

                           
                     
                                         
                        
                                      
 

class ContextValue {
                     

    constructor(context         ) {
        this.context = context;
    }

    static equal(a, b)          {
        return util.deepEqual(a, b);
    }
}

class ClearColor extends ContextValue                         {
    static default() { return Color.transparent; }

    set(v       )       {
        this.context.gl.clearColor(v.r, v.g, v.b, v.a);
    }
}

class ClearDepth extends ContextValue                          {
    static default() { return 1; }

    set(v        )       {
        this.context.gl.clearDepth(v);
    }
}

class ClearStencil extends ContextValue                          {
    static default() { return 0; }

    set(v        )       {
        this.context.gl.clearStencil(v);
    }
}

class ColorMask extends ContextValue                                 {
    static default() { return [true, true, true, true]; }

    set(v               )       {
        this.context.gl.colorMask(v[0], v[1], v[2], v[3]);
    }
}

class DepthMask extends ContextValue                           {
    static default() { return true; }

    set(v         )       {
        this.context.gl.depthMask(v);
    }
}

class StencilMask extends ContextValue                          {
    static default() { return 0xFF; }

    set(v        )       {
        this.context.gl.stencilMask(v);
    }
}

class StencilFunc extends ContextValue                                   {
    static default(context         ) {
        return {
            func: context.gl.ALWAYS,
            ref: 0,
            mask: 0xFF
        };
    }

    set(v                 )       {
        this.context.gl.stencilFunc(v.func, v.ref, v.mask);
    }
}

class StencilOp extends ContextValue                                 {
    static default(context         ) {
        const gl = context.gl;
        return [gl.KEEP, gl.KEEP, gl.KEEP];
    }

    set(v               )       {
        this.context.gl.stencilOp(v[0], v[1], v[2]);
    }
}

class StencilTest extends ContextValue                           {
    static default() { return false; }

    set(v         )       {
        const gl = this.context.gl;
        if (v) {
            gl.enable(gl.STENCIL_TEST);
        } else {
            gl.disable(gl.STENCIL_TEST);
        }
    }
}

class DepthRange extends ContextValue                                  {
    static default() { return [0, 1]; }

    set(v                )       {
        this.context.gl.depthRange(v[0], v[1]);
    }
}

class DepthTest extends ContextValue                           {
    static default() { return false; }

    set(v         )       {
        const gl = this.context.gl;
        if (v) {
            gl.enable(gl.DEPTH_TEST);
        } else {
            gl.disable(gl.DEPTH_TEST);
        }
    }
}

class DepthFunc extends ContextValue                                 {
    static default(context         ) {
        return context.gl.LESS;
    }

    set(v               )       {
        this.context.gl.depthFunc(v);
    }
}

class Blend extends ContextValue                           {
    static default() { return false; }

    set(v         )       {
        const gl = this.context.gl;
        if (v) {
            gl.enable(gl.BLEND);
        } else {
            gl.disable(gl.BLEND);
        }
    }
}

class BlendFunc extends ContextValue                                 {
    static default(context         ) {
        const gl = context.gl;
        return [gl.ONE, gl.ZERO];
    }

    set(v               )       {
        this.context.gl.blendFunc(v[0], v[1]);
    }
}

class BlendColor extends ContextValue                         {
    static default() { return Color.transparent; }

    set(v       )       {
        this.context.gl.blendColor(v.r, v.g, v.b, v.a);
    }
}

class Program extends ContextValue                                 {
    static default() { return null; }

    set(v               )       {
        this.context.gl.useProgram(v);
    }

    static equal(a               , b               )          {
        return a === b;
    }
}

class LineWidth extends ContextValue                          {
    static default() { return 1; }

    set(v        )       {
        this.context.gl.lineWidth(v);
    }
}

class ActiveTextureUnit extends ContextValue                                   {
    static default(context         ) {
        return context.gl.TEXTURE0;
    }

    set(v                 )       {
        this.context.gl.activeTexture(v);
    }
}

class Viewport extends ContextValue                                {
    static default(context         ) {
        const gl = context.gl;
        return [0, 0, gl.drawingBufferWidth, gl.drawingBufferHeight];
    }

    set(v              )       {
        this.context.gl.viewport(v[0], v[1], v[2], v[3]);
    }
}

class BindFramebuffer extends ContextValue                                     {
    static default() { return null; }

    set(v                   )       {
        const gl = this.context.gl;
        gl.bindFramebuffer(gl.FRAMEBUFFER, v);
    }

    static equal(a                   , b                   )          {
        return a === b;
    }
}

class BindRenderbuffer extends ContextValue                                      {
    static default() { return null; }

    set(v                    )       {
        const gl = this.context.gl;
        gl.bindRenderbuffer(gl.RENDERBUFFER, v);
    }

    static equal(a                    , b                    )          {
        return a === b;
    }
}

class BindTexture extends ContextValue                                 {
    static default() { return null; }

    set(v               )       {
        const gl = this.context.gl;
        gl.bindTexture(gl.TEXTURE_2D, v);
    }

    static equal(a               , b               )          {
        return a === b;
    }
}

class BindVertexBuffer extends ContextValue                                {
    static default() { return null; }

    set(v              )       {
        const gl = this.context.gl;
        gl.bindBuffer(gl.ARRAY_BUFFER, v);
    }

    static equal(a              , b              )          {
        return a === b;
    }
}

class BindElementBuffer extends ContextValue                                {
    static default() { return null; }

    set(v              )       {
        const gl = this.context.gl;
        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, v);
    }

    static equal()          {
        // Always rebind:
        return false;
    }
}

class BindVertexArrayOES extends ContextValue                       {
    static default() { return null; }

    set(v      )       {
        const context = this.context;
        if (context.extVertexArrayObject) {
            context.extVertexArrayObject.bindVertexArrayOES(v);
        }
    }

    static equal(a, b)          {
        return a === b;
    }
}

class PixelStoreUnpack extends ContextValue                          {
    static default() { return 4; }

    set(v        )       {
        const gl = this.context.gl;
        gl.pixelStorei(gl.UNPACK_ALIGNMENT, v);
    }
}

class PixelStoreUnpackPremultiplyAlpha extends ContextValue                           {
    static default() { return false; }

    set(v         )       {
        const gl = this.context.gl;
        gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, (v     ));
    }
}

/**
 * Framebuffer values
 */
class FramebufferValue extends ContextValue {
                     
                             

    constructor(context         , parent                  ) {
        super(context);
        this.parent = parent;
    }
}

class ColorAttachment extends FramebufferValue                                 {
    static default() { return null; }

    set(v               )       {
        const gl = this.context.gl;
        this.context.bindFramebuffer.set(this.parent);
        // note: it's possible to attach a renderbuffer to the color
        // attachment point, but thus far MBGL only uses textures for color
        gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0, gl.TEXTURE_2D, v, 0);
    }

    static equal(a, b)          {
        return a === b;
    }
}

class DepthAttachment extends FramebufferValue                                      {
    static default() { return null; }

    set(v                    )       {
        const gl = this.context.gl;
        this.context.bindFramebuffer.set(this.parent);
        // note: it's possible to attach a texture to the depth attachment
        // point, but thus far MBGL only uses renderbuffers for depth
        gl.framebufferRenderbuffer(gl.FRAMEBUFFER, gl.DEPTH_ATTACHMENT, gl.RENDERBUFFER, v);
    }

    static equal(a, b)          {
        return a === b;
    }
}

module.exports = {
    ClearColor,
    ClearDepth,
    ClearStencil,
    ColorMask,
    DepthMask,
    StencilMask,
    StencilFunc,
    StencilOp,
    StencilTest,
    DepthRange,
    DepthTest,
    DepthFunc,
    Blend,
    BlendFunc,
    BlendColor,
    Program,
    LineWidth,
    ActiveTextureUnit,
    Viewport,
    BindFramebuffer,
    BindRenderbuffer,
    BindTexture,
    BindVertexBuffer,
    BindElementBuffer,
    BindVertexArrayOES,
    PixelStoreUnpack,
    PixelStoreUnpackPremultiplyAlpha,

    ColorAttachment,
    DepthAttachment,
};
