//      

                                   

class State    {
                    
               
                   

    constructor(v          ) {
        this.value = v;
        this.current = v.constructor.default(v.context);
    }

    get()    {
        return this.current;
    }

    set(t   ) {
        if (this.dirty || !this.value.constructor.equal(this.current, t)) {
            this.current = t;
            this.value.set(t);
        }
    }

    isDirty()          {
        return this.dirty;
    }

    setDirty()       {
        this.dirty = true;
    }
}

module.exports = State;
